    const recordOpen = Boolean(recordAnchorEl);
    const recordHandleClick = (event: React.MouseEvent<HTMLElement>) => {
        setRecordAnchorEl(event.currentTarget);
    };
    const recordHandleClose = () => {
        setRecordAnchorEl(null);
    };
    const handleRecordSelected = (mbs: string) =>
    {
        const isExist = _.includes(recordSelected, mbs)
        if (isExist){ setRecordSelected(_.sortBy(recordSelected.filter(x=> x!== mbs))) }
        else{setRecordSelected(_.sortBy([...recordSelected,mbs]))}
    }
    const handleRecordSelectedAll = () =>
    {
        if (recordSelected.length !== recordCombination.length)
        {
            setRecordSelected(recordCombination)
        }
        else
        {
            setRecordSelected([])
        }
    }

    // for tickets line chart
    const [ticketCombination, setTicketCombination] = useState<string[]>([])
