export const fetchRecordsData = (market: string, banner: string[], startDate: string, endDate: string, compareStartDate: string, compareEndDate: string) => async (dispatch:any) => {

    dispatch({ type: FETCH_RECORDS_DATA, payload: true })

    const bannerFetch = "".concat(...banner.map(ban => `banner=${ ban }&`)) 
    const payload = await web.get(`/v1/masterdata/records?${bannerFetch}market=${market}&startdate=${startDate}&enddate=${endDate}&comparestartdate=${compareStartDate}&compareenddate=${compareEndDate}`)

    const newData = payload.data.data?.map((row: any,index: number) =>
    {
        const tempArray = payload.data.data.slice(0, index)
        const tempResult:any = {date: row.date}
        _.keys(row).filter(x => x !== 'date').map((key: any) =>
        {
            tempResult[key] = _.sumBy(tempArray, key)
            return true
        })
        return tempResult
    })

    dispatch({
        type: FETCH_RECORDS_DATA,
        payload: newData
    })

    dispatch({ type: FETCH_RECORDS_DATA, payload: false })

    return payload
}

