export const fetch_records_details = (market: string, banner: string[], startDate: string, endDate: string, compareStartDate: string, compareEndDate: string) => async (dispatch:any) => {

    dispatch({ type: LOADING, payload: true })

    const bannerFetch = "".concat(...banner.map(ban => `banner=${ ban }&`))
    const payload = await web.get(`/v1/masterdata/records/details?${ bannerFetch }market=${ market }&startdate=${ startDate }&enddate=${ endDate }&comparestartdate=${ compareStartDate }&compareenddate=${ compareEndDate }`)
    const rowData = payload.data.data

    //f1 (for bar chart statistic data)
    const f1Datas = _.flatMapDeep(_.values(rowData.data))
    let f1Result: any = []
    if (f1Datas.length > 0)
    {
        const keys = _.keys(f1Datas[0]).filter(k => k !== 'date')
        //only retrieve keys of current values
        f1Result = keys.filter(x=> _.startsWith(x,'_') === false ).map((item) =>
        {
            return {
                key: columnMapping[item],
                value: _.sumBy(f1Datas, item),
                lastValue: _.sumBy(f1Datas, `_${item}`)
                
            }
        })
    }

    //f2 (for line chart statistic with date data)
    const groupDateData = _.groupBy(f1Datas, 'date')
    const f2tempResult = _.keys(groupDateData).map(da =>
    {
        return groupDateData[da].reduce((item1, item2) =>
        {
            let sum:any = {};

            _.keys(item1).forEach(key => {
                if (item2.hasOwnProperty(key))
                {
                    if (key === 'date')
                    {
                        sum[key] = item1[key]
                    }
                    else
                    {
                        sum[key] = item1[key] + item2[key]
                    }
                }  
            })
            return sum;
        })
    })
    const f2Result = f2tempResult.map((row: any,index: number) =>
    {
        const tempArray = f2tempResult.slice(0, index)
        const tempResult:any = {date: row.date}
        _.keys(row).filter(x => x !== 'date').map((key: any) =>
        {
            tempResult[key] = _.sumBy(tempArray, key)
            return true
        })
        return tempResult
    })

    //tableData
    const statisticData:any = {}

    _.keys(rowData.data).map( banner =>{
        
        const choosenPeriod = `${rowData.startDate} to ${rowData.endDate}`
        const comparePeriod = `${rowData.compareStartDate} to ${rowData.compareEndDate}`
        const periodValue = [choosenPeriod, comparePeriod]
        
        const result = periodValue.map((eachPeriod,index) => {
            const dataList = rowData.data[banner]

            const tempDict:any = {
                period: eachPeriod
            }
            
            // filter date column and each period value
            _.keys(dataList[0]).filter(x => x !== 'date' && (index === 0 ? !_.startsWith(x, '_') : _.startsWith(x, '_'))).map((ke: any) =>
            {
                const key = _.startsWith(ke,'_') ? _.replace(ke,'_','') : ke
                tempDict[key] = _.sumBy(dataList, ke)
                return true
            })
            return tempDict
        })
        statisticData[banner] = result
        return true
    })

    const result = {
        p1: f1Result,
        p2: f2Result,
        statisticData: statisticData
    }
    dispatch({
        type:FETCH_LANDING_DETAILS,
        payload:result
    })

    dispatch({ type: LOADING, payload: false })
}


export const fetch_genders_details = (market: string, banner: string[], startDate: string, endDate: string, compareStartDate: string, compareEndDate: string) => async (dispatch: any) =>
{

    dispatch({ type: LOADING, payload: true })

    const bannerFetch = "".concat(...banner.map(ban => `banner=${ ban }&`))
    const payload = await web.get(`/v1/masterdata/genders/details?${ bannerFetch }market=${ market }&startdate=${ startDate }&enddate=${ endDate }&comparestartdate=${ compareStartDate }&compareenddate=${ compareEndDate }`)
    const rowData = payload.data.data

    if (rowData.data.length > 0)
    {
        let f1Result: any = []
        const keys = _.keys(rowData.data[0]).filter(k => k !== 'date')
        //only retrieve keys of current values
        f1Result = keys.filter(x => _.startsWith(x, '_') === false).map((item) =>
        {
            return {
                key: item,
                name: item,
                value: _.sumBy(rowData.data, item),
                lastValue: _.sumBy(rowData.data, `_${ item }`)
                    
            }
        })

        //statisticData
        let statisticData: any = []

        const choosenPeriod = `${ rowData.startDate } to ${ rowData.endDate }`
        const comparePeriod = `${ rowData.compareStartDate } to ${ rowData.compareEndDate }`
        const periodValue = [choosenPeriod, comparePeriod]

        const statisticResult = periodValue.map((eachPeriod, index) =>
        {
            const dataList = rowData.data

            const tempDict: any = {
                period: eachPeriod
            }
                
            // filter date column and each period value
            _.keys(dataList[0]).filter(x => x !== 'date' && (index === 0 ? !_.startsWith(x, '_') : _.startsWith(x, '_'))).map((ke: any) =>
            {
                const key = _.startsWith(ke, '_') ? _.replace(ke, '_', '') : ke
                tempDict[key] = _.sumBy(dataList, ke)
                return true
            })
            return tempDict
        })

        statisticData = statisticResult

        const result = {
            p1: f1Result,
            p2: f1Result,
            statisticData: statisticData
        }
            
        dispatch({
            type: FETCH_LANDING_DETAILS,
            payload: result
        })
    }

    dispatch({ type: LOADING, payload: false })
}

