import { CSVLink } from 'react-csv';
import Button from '@mui/material/Button';
import DownloadIcon from '@mui/icons-material/Download';
import {
    Typography,
} from '@material-ui/core';

export interface downloadHeaderType 
{
    label: string,
    key: string,
}

interface downloadType
{
    header: downloadHeaderType[],
    data: any[],
    fileName: string
}

export const DownloadCSV = (props:downloadType) =>
{
    return (
        <CSVLink  filename={props.fileName} data={props.data} headers={props.header} style={{textDecoration: 'none'}}>
            <Button style={{textTransform:'none',marginRight:'10px'}}  >
                <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'Export data'}</Typography>
                    <DownloadIcon style={{ color: '#5A91FF', width: '22px', height: '22px' }} />
            </Button>
        </CSVLink>
    )
}