const LandingPages = (props:Props) :React.ReactElement<Props>  =>  {

    const classes = useStyles()
    const dispatch = useDispatch()
    const theme = useTheme()
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));

    const [isFocusStartDate, setIsFocusStartDate] = React.useState<boolean>(true);
    
    const filterColumn: FilterType = useSelector((state: RootState) => state.landingFilter)
    const dataPool = useSelector((state: RootState)=> state.landingData)
    const landingTab: number = useSelector((state: RootState) => state.landginTab)
    
    const [alertErrorOpen, setAlertErrorOpen] = React.useState<boolean>(false);
    const [alertWording, setAlertWording] = React.useState<string>('');
    

    const [tempTimePeriod, setTempTimePeriod] = useState(filterColumn.period)
    const [tempStartDate, setTempStartDate] = useState(filterColumn.startDate)
    const [tempEndDate, setTempEndDate] = useState(filterColumn.endDate)
    const [tempCompareDateRange, setTempCompareDateRange] = useState(filterColumn.compareDateRange)

    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);
    const handleClick = (event: React.MouseEvent<HTMLElement>) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };
   
    const bannerData = [
        { key: 'KFC', name: 'KFC', value: 'KFC' },
        { key: 'PHHK', name: 'Pizza Hut', value: 'PHHK' },
        { key: 'PHDHK', name: 'PHD', value: 'PHDHK' }
    ]

    useEffect(() =>
    {
        if (_.isEmpty(dataPool.profiles.data))
        {
            dispatch(fetchProfilesData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.ageRanges.data.length === 0)
        {
            dispatch(fetchAgeRangesData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.records.data.length === 0)
        {
            dispatch(fetchRecordsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.tickets.data.length === 0)
        {
            dispatch(fetchTicketsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.salesAmounts.data.length === 0)
        {
            dispatch(fetchSalesAmountsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (_.isEmpty(dataPool.genders.data))
        {
            dispatch(fetchGenderData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.traffics.data.length === 0)
        {
            dispatch(fetchTrafficsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.trafficSources.data.length === 0)
        {
            dispatch(fetchTrafficSourcesData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (_.isEmpty(dataPool.funnels.data))
        {
            dispatch(fetchFunnelsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.conversions.data.length === 0)
        {
             dispatch(fetchConversionsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.members.data.length === 0)
        {
            dispatch(fetchMembersData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
        if (dataPool.purchases.data.length === 0)
        {
            dispatch(fetchPurchasesData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }    
        if (_.isEmpty(dataPool.topDatas.data))
        {
            dispatch(fetchTopsData(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
        }
    }, [])
    
    const isInitialMount = useRef(true);
    useEffect(() =>
    {
        if (isInitialMount.current) {
            isInitialMount.current = false;
        } else {
            dispatch(fetchProfilesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchAgeRangesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchRecordsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchTicketsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchSalesAmountsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchGenderData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchTrafficsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchTrafficSourcesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchFunnelsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchConversionsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchMembersData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchPurchasesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
            dispatch(fetchTopsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))  
        }
    }, [filterColumn.banner,filterColumn.market])


    const handleMarketChange = (column:string, value:string, banner:string[]) => {
        dispatch(handleLandingFilter({column, value}))
    }
    
    const handleBannerChange = (column: string, value: string[], market: string) =>
    {
        if (value.length === 0)
        {
            setAlertWording('Please select at latest one banner .')
            setAlertErrorOpen(true)
            return true;    
        }
        dispatch(handleLandingFilter({column, value}))
    }

    const handlePeriodChange = (periodOption:string) => {

        let startDate = moment().format('YYYY-MM-DD')
        let endDate = moment().format('YYYY-MM-DD')
        let compareStartDate = moment().subtract(14,'days').format('YYYY-MM-DD')
        let compareEndDate = moment().subtract(8,'days').format('YYYY-MM-DD')
        switch (periodOption) {
            case 'Last 7 days':
                startDate =  moment().subtract(7,'days').format('YYYY-MM-DD')
                endDate = moment().format('YYYY-MM-DD')
                compareStartDate = moment().subtract(15,'days').format('YYYY-MM-DD')
                compareEndDate = moment().subtract(8,'days').format('YYYY-MM-DD')
                break;
            case 'Last 30 days':
                startDate = moment().subtract(30,'days').format('YYYY-MM-DD')
                endDate = moment().format('YYYY-MM-DD')
                compareStartDate = moment().subtract(61,'days').format('YYYY-MM-DD')
                compareEndDate = moment().subtract(31,'days').format('YYYY-MM-DD')
                break;
            case 'Last 90 days':
                startDate = moment().subtract(90,'days').format('YYYY-MM-DD')
                endDate = moment().format('YYYY-MM-DD')
                compareStartDate = moment().subtract(181,'days').format('YYYY-MM-DD')
                compareEndDate = moment().subtract(91,'days').format('YYYY-MM-DD')
                break;
            case 'Last 365 days':
                startDate = moment().subtract(365,'days').format('YYYY-MM-DD')
                endDate = moment().format('YYYY-MM-DD')
                compareStartDate = moment().subtract(731,'days').format('YYYY-MM-DD')
                compareEndDate = moment().subtract(366,'days').format('YYYY-MM-DD')
                break;       
            default:
                startDate =  moment().subtract(7,'days').format('YYYY-MM-DD')
                endDate = moment().format('YYYY-MM-DD')
                compareStartDate = moment().subtract(15,'days').format('YYYY-MM-DD')
                compareEndDate = moment().subtract(8,'days').format('YYYY-MM-DD')
                break;
        }

        setTempTimePeriod(periodOption)
        setTempStartDate(startDate)
        setTempEndDate(endDate)
        setTempCompareDateRange([compareStartDate, compareEndDate])
    } 


    const handleDatePeriodChange = (dateRange: (DateObject | DateObject[] | null), isFocusStartDate: boolean) => {
        if(dateRange){
            const range = dateRange as DateObject[]
            if(range.length === 2){
                const r = range.map(x=>{
                    return x.toString()
                })
                if (isFocusStartDate === true)
                {
                    setTempStartDate(r[0])
                    setTempEndDate(r[1])
                }
                else
                {
                    setTempCompareDateRange(r)
                }
            }
        }
    }

    const pullingNewData = () =>
    {
        dispatch(handleLandingFilterPeriod({
            periodOption: tempTimePeriod,
            startDate: tempStartDate,
            endDate: tempEndDate,
            compareStartDate: tempCompareDateRange[0],
            compareEndDate: tempCompareDateRange[1]
        }))
        dispatch(fetchProfilesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchAgeRangesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchRecordsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchTicketsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchSalesAmountsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchGenderData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchTrafficsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchTrafficSourcesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchFunnelsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchConversionsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchMembersData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchPurchasesData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
        dispatch(fetchTopsData(filterColumn.market, filterColumn.banner, tempStartDate, tempEndDate, tempCompareDateRange[0], tempCompareDateRange[1]))
    }

    const dateOptions = 
    [
        {
            id:0,
            value:'Last 7 days',
            name:'Last 7 days'
        },
        {
            id:1,
            value:'Last 30 days',
            name:'Last 30 days'
        },
        {
            id:2,
            value:'Last 90 days',
            name:'Last 90 days'
        },
        {
            id:3,
            value:'Last 365 days',
            name:'Last 365 days'
        }
    ]

    return(
        <Grid container direction='column' alignItems='center' className={classes.main}>
            <Grid container direction='column' style={{ paddingRight:'3%',paddingLeft:'3%'}}>
                <Grid container direction='row' justifyContent='space-between' alignItems='center' style={{marginTop:'40px'}} className={classes.recordBanner}>
                    <Grid item style={{paddingLeft:'40px'}}>
                        <Typography className={classes.recordFont}>{'JRG ID Records Overview'}</Typography>
                    </Grid>
                    <Grid item style={{paddingRight:'40px'}}>
                        <Grid container>
                            <Grid item style={{marginRight:'20px'}}> 
                                <Grid container direction='column' justifyContent='center'>
                                    <Grid item>
                                        <Typography className={classes.recordFontSm} >{'Banner'}</Typography>
                                    </Grid>
                                    <Grid item  style={{marginTop:'4px'}}>
                                        <FormControl>
                                            <Select
                                                multiple
                                                labelId="demo-simple-select-helper-label"
                                                id="demo-simple-select-helper"
                                                value={filterColumn.banner}
                                                onChange={(event) =>
                                                {
                                                    handleBannerChange('banner', event.target.value as string[], filterColumn.market)
                                                }}
                                                style={{ width: '280px', height: '32px' }}
                                                variant="outlined"
                                                renderValue={(selected) =>
                                                {
                                                    return (
                                                        <Grid container alignItems='center'>
                                                            {selected.map((ban,index) =>
                                                            {
                                                                const newBanner = bannerMappings[ban]
                                                                return (
                                                                    <Grid item key={ban}>
                                                                        <Grid container alignItems='center'>
                                                                            {
                                                                                ban === 'KFC' ? 
                                                                                    <img src={KFC} style={{width:'20px',height:'20px',paddingRight:'6px'}}/>
                                                                                    : ban === 'PHHK' ? 
                                                                                        <img src={PIZZA} style={{width:'20px',height:'20px',paddingRight:'6px'}}/>
                                                                                    :  <img src={PHD} style={{width:'20px',height:'20px',paddingRight:'6px'}}/>
                                                                            }
                                                                            
                                                                            <Grid item>
                                                                                <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{newBanner}</Typography>
                                                                            </Grid>
                                                                            { index === selected.length - 1 ? '' : <Typography style={{margin:'0px 4px'}}>{','}</Typography> }
                                                                        </Grid>
                                                                    </Grid>
                                                                )
                                                            })}
                                                        </Grid>
                                                    )
                                                }}
                                            >
                                            {
                                                bannerData.map(ban =>
                                                {
                                                    return (
                                                         <MenuItem key={ban.value} value={ban.value}>
                                                            <Grid container alignItems='center'>
                                                                {_.includes(filterColumn.banner,ban.value) ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                                {
                                                                    ban.value === 'KFC' ? 
                                                                        <img src={KFC} style={{width:'20px',height:'20px',paddingRight:'10px'}}/>
                                                                        : ban.value === 'PHHK' ? 
                                                                            <img src={PIZZA} style={{width:'20px',height:'20px',paddingRight:'10px'}}/>
                                                                        :  <img src={PHD} style={{width:'20px',height:'20px',paddingRight:'10px'}}/>
                                                                }
                                                                <Grid item>
                                                                    <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{ban.name}</Typography>
                                                                </Grid>
                                                            </Grid>
                                                        </MenuItem>
                                                    )
                                                })
                                            }
                                            </Select>
                                        </FormControl>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item style={{marginRight:'20px'}}> 
                                <Grid container direction='column' justifyContent='center'>
                                    <Grid item>
                                        <Typography className={classes.recordFontSm}>{'Market'}</Typography>
                                    </Grid>
                                    <Grid item  style={{marginTop:'4px'}}>
                                        <FormControl fullWidth>
                                            <Select
                                                labelId="demo-simple-select-helper-label"
                                                id="demo-simple-select-helper"
                                                value={filterColumn.market}
                                                onChange={(event)=> { 
                                                    handleMarketChange('market', event.target.value, filterColumn.banner)
                                                }}
                                                style={{width:'150px',height:'32px'}}
                                                variant="outlined"
                                            >
                                                <MenuItem value={'HK'}>
                                                    <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{'HongKong'}</Typography>
                                                </MenuItem>
                                            <MenuItem value={'TW'}><Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{'Taiwan'}</Typography></MenuItem>
                                            <MenuItem value={'VN'}><Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{'Vietnam'}</Typography></MenuItem>
                                            </Select>
                                        </FormControl>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item style={{marginRight:'10px'}}> 
                                <Grid container direction='column' justifyContent='center' >
                                    <Grid item>
                                        <Typography className={classes.recordFontSm}>{'Date'}</Typography>
                                    </Grid>
                                    <Grid item  style={{marginTop:'4px'}}>
                                            <Button
                                                disableRipple
                                                aria-haspopup="true"
                                                aria-expanded={open ? 'true' : undefined}
                                                onClick={handleClick}
                                                variant='outlined'
                                                sx={ {'&:hover': { bgcolor: 'transparent' }}}
                                                style={{width:'230px',height:'32px',border:'1px solid #626262', borderRadius:5, color:'#000'}}
                                            >
                                                <Grid container justifyContent='space-between'>
                                                    <Grid item>
                                                        <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>{`${filterColumn.startDate} - ${filterColumn.endDate}`}</Typography>
                                                    </Grid>
                                                    <ArrowDropDownIcon/>
                                                </Grid>
                                            </Button>
                                            <Menu
                                                style={{padding:0}}
                                                id="basic-menu"
                                                anchorEl={anchorEl}
                                                open={open}
                                                onClose={handleClose}
                                                anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
                                                transformOrigin={{ vertical: "top", horizontal: "right" }}
                                                MenuListProps={{
                                                    'aria-labelledby': 'basic-button',
                                                    style:{padding:0,width:'1050px'}
                                                }}
                                            >
                                                <Grid container style={{width:'100%'}}
                                                >
                                                    <Grid item>
                                                        <Calendar
                                                            range
                                                            format='YYYY-MM-DD'
                                                            numberOfMonths={2}
                                                            className={classes.calendarStyle}
                                                            disabled={!filterColumn.isCustomCompareChecked && tempTimePeriod !== 'custom'}
                                                            onChange={(value) =>
                                                                handleDatePeriodChange(value,isFocusStartDate)
                                                            }
                                                        >
                                                
                                                        </Calendar>
                                                    </Grid>
                                                    <Grid item style={{height:'320px',border:'1px solid #E0E0E0'}}>
                                                    
                                                    </Grid>
                                                    <Grid item style={{width:'380px'}}>
                                                        <Grid container direction='column' style={{padding:'15px 30px'}}>
                                                            <Grid item style={{marginBottom:'4px'}}>
                                                                <Typography style={{fontFamily:'Noto sans'}}>{'Date Range'}</Typography>
                                                            </Grid>
                                                            <Grid item style={{marginBottom:'12px'}}>
                                                                <FormControl fullWidth>
                                                                    <Select
                                                                        value={tempTimePeriod}
                                                                        onChange={(event)=> handlePeriodChange(event.target.value)}
                                                                        labelId="demo-simple-select-helper-label"
                                                                        id="demo-simple-select-helper"
                                                                        style={{width:'100%',height:'32px'}}
                                                                        variant="outlined"
                                                                        renderValue={(value) => {  return value }}
                                                                    >
                                                                        {dateOptions.map(x=>{
                                                                            return <MenuItem key={x.id} value={x.value}>
                                                                                <Grid container alignItems='center'>
                                                                                    {tempTimePeriod === x.value ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                                                    <Grid item>
                                                                                        <Typography>{x.name}</Typography>
                                                                                    </Grid>
                                                                                </Grid>
                                                                            </MenuItem>
                                                                        })}
                                                                    <Divider />
                                                                    <MenuItem value={'custom'}>
                                                                        <Grid container alignItems='center'>
                                                                            {tempTimePeriod === 'custom' ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                                            <Grid item>
                                                                                <Typography>{'custom'}</Typography>
                                                                            </Grid>
                                                                        </Grid>
                                                                    </MenuItem>
                                                                    </Select>
                                                                </FormControl>
                                                            </Grid> 
                                                            <Grid item container alignItems='center'>
                                                                <Grid item>
                                                                    <TextField InputProps={{
                                                                        style: {width:'150px',height:'32px'}
                                                                }}
                                                                    onFocus={()=> setIsFocusStartDate(true)}
                                                                    placeholder='Start date'
                                                                    value={tempStartDate}
                                                                    disabled={tempTimePeriod !== 'custom'}
                                                                >
                                                                    
                                                                    </TextField>
                                                                </Grid>
                                                                <Grid item style={{margin:'0px 7px'}}>
                                                                    <Typography>{'-'}</Typography>
                                                                </Grid>
                                                                <Grid item>
                                                                    <TextField InputProps={{
                                                                        style: {width:'150px',height:'32px'}
                                                                }}
                                                                    onFocus={()=> setIsFocusStartDate(true)}
                                                                    placeholder='End date'
                                                                    value={tempEndDate}
                                                                    disabled={tempTimePeriod !== 'custom'}
                                                                >
                                                                    
                                                                    </TextField>
                                                                </Grid>
                                                            </Grid>
                                                            <Grid item container alignItems='center'>
                                                                <Checkbox sx={{
                                                                        '&:hover': { bgcolor: 'transparent' },
                                                                        marginLeft:'-10px'
                                                                    }}
                                                                    checked={filterColumn.isCustomCompareChecked}
                                                                    onChange={() => dispatch(handleIsCustomCompareChecked())}
                                                                    disabled={tempTimePeriod !== 'custom'}
                                                                    disableRipple/>
                                                                <Typography style={{color: tempTimePeriod !== 'custom' ? 'rgba(0, 0, 0, 0.38)' :'#000'}}>{'Compare to:'}</Typography>
                                                            </Grid>
                                                            <Grid item container alignItems='center'>
                                                                <Grid item>
                                                                    <TextField
                                                                        InputProps={{
                                                                        style: { width: '150px', height: '32px' }
                                                                            
                                                                    }}
                                                                    placeholder='Compare start date'
                                                                    disabled={!filterColumn.isCustomCompareChecked || tempTimePeriod !== 'custom'}
                                                                    value={tempCompareDateRange[0]}
                                                                    onFocus={()=> setIsFocusStartDate(false)}
                                                                    >
                                                                    </TextField>
                                                                </Grid>
                                                                <Grid item style={{margin:'0px 7px'}}>
                                                                    <Typography>{'-'}</Typography>
                                                                </Grid>
                                                                <Grid item>
                                                                    <TextField InputProps={{
                                                                        style: {width:'150px',height:'32px'}
                                                                }}
                                                                    placeholder='Compare end date'
                                                                    disabled={!filterColumn.isCustomCompareChecked || tempTimePeriod !== 'custom'}
                                                                    value={tempCompareDateRange[1]}
                                                                    onFocus={()=> setIsFocusStartDate(false)}
                                                                >
                                                                    
                                                                    </TextField>
                                                                </Grid>
                                                            </Grid>
                                                            <Grid item container alignItems='center' justifyContent='flex-end' style={{marginTop:'30px'}}>
                                                                 <Grid item style={{marginRight:'22px'}}>
                                                                <Button onClick={handleClose} variant="contained" disableRipple style={{ border: '2px solid #5A91FF', backgroundColor: '#F1F4F6', color: '#5A91FF' }} className={classes.nextButton} >
                                                                    
                                                                    <Typography style={{color:'#5A91FF',fontFamily:'Noto sans',textTransform:'none'}}>{'Cancel'}</Typography>
                                                                </Button>
                                                                </Grid>
                                                                <Grid item>
                                                                <Button onClick={() => pullingNewData()} variant="contained" disableRipple className={classes.nextButton}>
                                                                        <Typography style={{color:'#FFF',fontFamily:'Noto sans',textTransform:'none'}}>{'Apply'}</Typography>
                                                                    </Button>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                            </Menu>
                                    </Grid>
                                </Grid>
                                
                                 <Grid item container justifyContent='center' style={{marginTop:'4px'}}> 
                                    <Grid item>
                                        <Typography style={{fontSize:'12px', fontFamily:'Noto sans', color:'#989898'}}>{`Compare to ${filterColumn.compareDateRange[0]} - ${filterColumn.compareDateRange[1]}`}</Typography>
                                    </Grid>
                                </Grid>
                                
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

                {/* tab */}
                <Grid item container style={{margin: matchesMac ? '30px 0px' :'40px 0px'}}>
                    <Grid item style={{marginRight:'20px'}}>
                        <Button disableRipple style={landingTab === 0 ? {backgroundColor:'#5A91FF',borderRadius:'25px',color:'#FFF'} : {backgroundColor:'transparent',borderRadius:'25px',color:'#000'}} className={classes.button}  onClick={()=> dispatch(handleLandingTab(0))}>
                            <Typography className={classes.tabButtonFont}>{'Master Data'}</Typography> 
                        </Button>
                    </Grid>
                    <Grid item style={{marginRight:'20px'}}>
                        <Button disableRipple style={landingTab === 1 ? {backgroundColor:'#5A91FF',borderRadius:'25px',color:'#FFF'} : {backgroundColor:'transparent',borderRadius:'25px',color:'#000'}} className={classes.button}  onClick={()=> dispatch(handleLandingTab(1))}>                   
                            <Typography className={classes.tabButtonFont}>{'Onsite Performance'}</Typography> 
                        </Button>
                    </Grid>
                    <Grid item style={{marginRight:'20px'}}>
                        <Button disableRipple style={landingTab === 2 ? {backgroundColor:'#5A91FF',borderRadius:'25px',color:'#FFF'} : {backgroundColor:'transparent',borderRadius:'25px',color:'#000'}} className={classes.button}  onClick={()=> dispatch(handleLandingTab(2))}>
                        <Typography className={classes.tabButtonFont}>{'Member'}</Typography> 
                        </Button>
                    </Grid> 
                </Grid>
                <Grid item container style={{marginBottom:'80px'}}>
                    {landingTab === 0 ? 
                        <MasterPage/>
                    : landingTab === 1 ? 
                        <PerformPage/>
                    :
                        <MemberPage/>
                    }
                </Grid>
            </Grid>
            <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'center' }} open={alertErrorOpen} autoHideDuration={3000} onClose={() => setAlertErrorOpen(false)}>
                <Grid container justifyContent='space-between' alignItems='center' className={classes.snackbar} >
                    <Grid item>
                        <Grid container>
                            <CancelOutlinedIcon style={{ color: '#FF4D4F', marginRight: '16px', width: '22px', height: '22px' }} />
                            <Typography>{alertWording}</Typography>
                        </Grid>
                        
                    </Grid>
                    <Grid item>
                        <Grid container alignItems='center'>                            
                            <ClearRoundedIcon onClick={()=>setAlertErrorOpen(false)} style={{color:'#989898',width:'22px',height:'22px'}}/>
                        </Grid>
                    </Grid>
                </Grid>   
            </Snackbar>
        </Grid>
    )
}

export default LandingPages