const L_OnsitePerformance = () =>  {

    const classes = useStyles()
    const dispatch = useDispatch()
    const history = useHistory()
    const theme = useTheme()
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));

    const [tabValue, setTabValue] = useState(0)

    const [trafficActiveLine, setTrafficActiveLine] = useState('');
    const [conversionActiveLine, setConversionActiveLine] = useState('');
    
    const masterData = useSelector((state: RootState) => state.landingData)
    const filterColumn = useSelector((state: RootState)=> state.landingFilter)

    const funnelColor = ['#4A78DF','#5A91FF','#6CB0FF','#83D5FF','#1DC0D7','#4CD4DD']

    const handleChange = (event: React.SyntheticEvent, newValue: number) => {
        setTabValue(newValue);
      };

    const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }: any) =>
    {
        const RADIAN = Math.PI / 180;
        const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
        const x = cx + radius * Math.cos(-midAngle * RADIAN);
        const y = cy + radius * Math.sin(-midAngle * RADIAN);

        return (
            <text x={x} y={y} fill="white" textAnchor="middle" dominantBaseline="central">
            {`${(percent * 100).toFixed(0)}%`}
            </text>
        );
    };

    const CustomLabel = (props:any) => {
        const { cx, cy } = props.viewBox;
        return (
        <React.Fragment >
            <text
            x={cx}
            y={cy - 15}
            fill="rgba(0, 0, 0, 0.87)"
            className="recharts-text recharts-label"
            textAnchor="middle"
            dominantBaseline="central"
            >
                <tspan alignmentBaseline="middle" fontSize={props.matchesMac ? '14px' : "18px"} fontFamily="Noto sans" fill="#0E0F0F">
                        
                {`Total`}
            </tspan>
            </text>
            <text
            x={cx}
            y={cy + 15}
            fill="rgba(0, 0, 0, 0.54)"
            className="recharts-text recharts-label"
            textAnchor="middle"
            dominantBaseline="central"
            >
                <tspan fontSize={props.matchesMac ? '16px' : "22px"} fontFamily="Noto sans" fill="#5A91FF">
                {props.value1}
            </tspan>
                    
            </text>
        
        </React.Fragment>
        )
    }
    
    // for traffic line chart
    const [trafficCombination, setTrafficCombination] = useState<string[]>([])
    const [trafficSelected, setTrafficSelected] = useState<string[]>([])
    const [trafficAnchorEl, setTrafficAnchorEl] = React.useState<null | HTMLElement>(null);
    const trafficOpen = Boolean(trafficAnchorEl);
    const trafficHandleClick = (event: React.MouseEvent<HTMLElement>) => {
        setTrafficAnchorEl(event.currentTarget);
    };
    const trafficHandleClose = () => {
        setTrafficAnchorEl(null);
    };
    const handleTrafficSelected = (mbs: string) =>
    {
        const isExist = _.includes(trafficSelected, mbs)
        if (isExist){ setTrafficSelected(_.sortBy(trafficSelected.filter(x=> x!== mbs))) }
        else{setTrafficSelected(_.sortBy([...trafficSelected,mbs]))}
    }
    const handleTrafficSelectedAll = () =>
    {
        if (trafficSelected.length !== trafficCombination.length)
        {
            setTrafficSelected(trafficCombination)
        }
        else
        {
            setTrafficSelected([])
        }
    }

    // for conversion line chart
    const [conversionCombination, setConversionCombination] = useState<string[]>([])
    const [conversionSelected, setConversionSelected] = useState<string[]>([])
    const [conversionAnchorEl, setConversionAnchorEl] = React.useState<null | HTMLElement>(null);
    const conversionOpen = Boolean(conversionAnchorEl);
    const conversionHandleClick = (event: React.MouseEvent<HTMLElement>) => {
        setConversionAnchorEl(event.currentTarget);
    };
    const conversionHandleClose = () => {
        setConversionAnchorEl(null);
    };
    const handleConversionSelected = (mbs: string) =>
    {
        const isExist = _.includes(conversionSelected, mbs)
        if (isExist){ setConversionSelected(_.sortBy(conversionSelected.filter(x=> x!== mbs))) }
        else{setConversionSelected(_.sortBy([...conversionSelected,mbs]))}
    }
    const handleConversionSelectedAll = () =>
    {
        if (conversionSelected.length !== conversionCombination.length)
        {
            setConversionSelected(conversionCombination)
        }
        else
        {
            setConversionSelected([])
        }
    }

    useEffect(() =>
    {
        if (masterData.traffics.data.length>0)
        {
            const keys = _.sortBy(_.keys(masterData.traffics.data[0]).filter((x: any) => x !== 'date' && _.startsWith(x, '_') === false))
            setTrafficCombination(keys)
            setTrafficSelected(keys)
            setConversionCombination(keys)
            setConversionSelected(keys)
        }
    }, [masterData.traffics.data])
    
    return(
        <Grid container direction='column' alignItems='center' className={classes.main}>
            
            <Grid item container alignItems='center' direction='column'>
                {/* Own Channel (App & Web) Traffic area */}
                <Grid container direction='row' justifyContent='center' >
                    <Grid item container justifyContent='flex-start' lg={8} md={8}>
                        <Grid item container style={{width:'98%'}}>
                            <Grid item container  direction='column' alignItems='center' className={classes.trafficCard} style={{marginBottom:'40px'}}>
                                <Grid container justifyContent='space-between' style={{ marginTop: matchesMac ? '15px' : '30px' , padding: matchesMac?'0px 25px':'0px 35px'}}>
                                    <Grid item >
                                        <Typography className={classes.subTitleFont} >{'Own Channel (App & Web) Traffic'}</Typography>
                                    </Grid>
                                    <Grid item >
                                        <Button
                                            startIcon={<AddIcon style={{color: '#5A91FF'}}/>}
                                            variant='outlined'
                                            style={{ width: '150px', height: '32px', border:'1px dashed #5A91FF' }}
                                            id="demo-positioned-button"
                                            aria-controls="demo-positioned-menu"
                                            aria-haspopup="true"
                                            aria-expanded={trafficOpen ? 'true' : undefined}
                                            onClick={trafficHandleClick}
                                        >
                                            <Typography style={{ textTransform: 'none', fontSize: '16px', color: '#5A91FF', fontFamily: 'Noto sans' }}>{'Comparison'}</Typography>
                                            
                                        </Button>
                                        <Menu
                                            id="demo-positioned-menu"
                                            aria-labelledby="demo-positioned-button"
                                            anchorEl={trafficAnchorEl}
                                            open={trafficOpen}
                                            onClose={trafficHandleClose}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                            }}
                                                transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                                }}
                                            MenuListProps={{
                                                'aria-labelledby': 'basic-button',
                                                style:{padding:0, width:'200px'}
                                            }}
                                        >
                                        <MenuItem style={{backgroundColor:trafficSelected.length === trafficCombination.length ? '#E2EEFF':''}} onClick={() => { handleTrafficSelectedAll() }}>
                                            <Grid container alignItems='center'>
                                                {trafficSelected.length === trafficCombination.length ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                <Grid item>
                                                    <Typography>{'All'}</Typography>
                                                </Grid>
                                            </Grid>
                                        </MenuItem>
                                        <Divider style={{width:'100%'}}/>
                                        {trafficCombination.map(bc =>
                                        {
                                            return <MenuItem key={bc} style={{backgroundColor:_.includes(trafficSelected,bc) ?'#E2EEFF':''}} onClick={() => { handleTrafficSelected(bc) }}>
                                                <Grid container alignItems='center'>
                                                    {_.includes(trafficSelected,bc) ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                    <Grid item>
                                                        <Typography>{memberTypeMappings[bc]}</Typography>
                                                    </Grid>
                                                </Grid>
                                            </MenuItem>
                                        })}
                                            
                                        </Menu>
                                    </Grid>
                                </Grid>
                                <Grid container direction={matchesMac?'column':'row'} justifyContent='space-between' style={{width:'94%',marginTop:'10px'}}>
                                    <Grid item >
                                        <Grid container alignItems='center'>
                                            {trafficSelected.map((ty) =>
                                            {
                                                return (
                                                    <Grid item key={ty}>
                                                        <Grid container direction='row' alignItems='center' style={{marginRight:'20px'}}>
                                                            <CropSquareIcon style={{width:'10px',height:'10px',marginRight:'8px',color: memberTypeColorMappings[ty], backgroundColor: memberTypeColorMappings[ty]}}/>
                                                            <Grid item>
                                                                <Typography style={{fontSize:'14px',fontFamily:'Noto sans'}}>{memberTypeMappings[ty]}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                )
                                            })}
                                        </Grid>
                                    </Grid>
                                    <Grid item style={{ marginTop: matchesMac ? '10px' : '0px'}}>
                                        <Grid container alignItems='center'>
                                            <Grid item style={{marginRight:'8px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}></Typography>
                                            </Grid>
                                            <Grid item style={{marginRight:'20px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>{dateOptionMappings[filterColumn.period]}</Typography>
                                            </Grid>
                                            <Grid item style={{marginRight:'8px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>---</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>{'Preceding period'}</Typography>
                                            </Grid>    
                                        </Grid>
                                    </Grid>
                                </Grid>
                                {masterData.traffics.isLoading ? 
                                <Grid container justifyContent='center' alignItems='center' style={{height:'260px'}}>
                                    <ReactLoading type={'bars'} color="#5A91FF" />
                                </Grid>
                                    : 
                                    <>
                                        <Grid container alignItems='center' style={{ width: '98%', paddingRight:'10px', height:'220px', marginTop: matchesMac?'20px':'30px'  }}> 
                                        <ResponsiveContainer width="100%" height="100%">
                                            <LineChart
                                            width={500}
                                            height={300}
                                            data={masterData.traffics.data}
                                            margin={{
                                                top: 5,
                                                right: 30,
                                                left: 20,
                                                bottom: 5,
                                            }}
                                            >
                                            <CartesianGrid   vertical={false} />
                                            <XAxis dataKey="date" tick={{color: '#989898', fontFamily:'Roboto', fontWeight:300, fontSize:'14px'}}/>
                                            <YAxis tick={{color: '#989898', fontFamily:'Roboto', fontWeight:300, fontSize:'14px'}}/>
                                                <Tooltip cursor={{ fill: '#0F0F0F', opacity: '10%' }} content={(payload) => CustomTooltipForLine(payload, memberTypeColorMappings, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange, trafficActiveLine)} />
                                                {
                                                    trafficSelected.map((dataKey,index) =>
                                                    {
                                                        return (
                                                            <React.Fragment key={dataKey}>
                                                                <Line type="linear" name={memberTypeMappings[dataKey]} dataKey={dataKey} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'3px'} style={{opacity: (trafficActiveLine !== dataKey && trafficActiveLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={()=> setTrafficActiveLine(dataKey)} onMouseLeave={()=> setTrafficActiveLine('')}/>
                                                                <Line type="linear" name={`_${ memberTypeMappings[dataKey] }`} dataKey={`_${ dataKey }`} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'1px'}  strokeDasharray="2 2" dot={false} style={{opacity: (trafficActiveLine !== `_${ dataKey }` && trafficActiveLine !== '') ? 0.2 : 1 }}  onMouseEnter={()=> setTrafficActiveLine(`_${ dataKey }`)} onMouseLeave={()=> setTrafficActiveLine('')}/>
                                                            </React.Fragment>
                                                        )
                                                    })
                                                }
                                            </LineChart>
                                        </ResponsiveContainer>
                                    </Grid>
                                    <Grid item container justifyContent='flex-end' alignItems='center' style={{ marginTop: '24px', paddingRight: '12px'  }}>
                                        <Button style={{textTransform:'none',marginRight:'10px'}} onClick={() => downloadFile(masterData.traffics.data, 'traffic.json')} >
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'Export data'}</Typography>
                                            <DownloadIcon style={{ color: '#5A91FF', width:'22px', height:'22px' }}/>
                                        </Button>
                                        <Button style={{textTransform:'none'}} onClick={() => history.push('/dashboard/traffics')}>
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'See more detail'}</Typography>
                                            <ArrowForwardIosIcon style={{ color: '#5A91FF', width:'16px', height:'16px' }}/>
                                        </Button>
                                    </Grid>
                                    </>
                                }
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item container justifyContent='flex-end' lg={4} md={4}>
                        <Grid item container style={{width:'98%'}}>
                            <Grid item container direction='column' className={classes.trafficCard} >
                                <Grid container style={{ marginTop: matchesMac ? '15px' : '30px' }}>
                                    <Grid item style={{padding: matchesMac?'0px 25px':'0px 40px'}}>
                                        <Typography className={classes.subTitleFont} >{'Traffic Sources'}</Typography>
                                    </Grid>
                                </Grid>
                                {masterData.trafficSources.isLoading ? 
                                <Grid container justifyContent='center' alignItems='center' style={{height:'260px'}}>
                                    <ReactLoading type={'bars'} color="#5A91FF" />
                                </Grid>
                                    : 
                                    <>
                                    <Grid container justifyContent='center' style={{height:'300px'}}>
                                        <Grid item lg={6} md={6} >
                                            <ResponsiveContainer width="100%" height="100%">
                                                <PieChart width={400} height={400}>
                                                <Pie
                                                    isAnimationActive={ false }
                                                    data={masterData.trafficSources.data}
                                                    cx="50%"
                                                    cy="50%"
                                                    labelLine={false}
                                                    label={renderCustomizedLabel}
                                                    outerRadius={matchesMac? 85 : 110}
                                                    innerRadius={matchesMac ? 50 : 70}
                                                    fill="#8884d8"
                                                    dataKey="value"
                                                >
                                                    <Label width={30} position="center" 
                                                        content={<CustomLabel value1={currencyFormat(_.sumBy(masterData.trafficSources.data,'value'))} matchesMac={matchesMac}/>}>
                                                    </Label>
                                                    {masterData.trafficSources.data.map((entry:any, index:any) =>
                                                    {
                                                        return (
                                                                <Cell key={`cell-${ index }`} fill={trafficTypeColorMappings[entry.key]} />
                                                            )
                                                        })
                                                    }
                                                    </Pie>
                                                    <Tooltip content={(payload) => CustomTooltipForPie(payload,filterColumn.startDate,  filterColumn.endDate, filterColumn.compareDateRange)} />
                                                </PieChart>
                                            </ResponsiveContainer>
                                        </Grid>
                                        <Grid item lg={6} md={6} style={{paddingLeft:'30px',marginTop:'25px'}}>
                                            <Grid container direction='column' justifyContent='center' alignItems='center' >
                                                {masterData.trafficSources.data.map((data:any,index:any) =>
                                                {
                                                    const changeRate = _.floor((data.value - data.lastValue)/ data.lastValue * 100 ,2)
                                                    return (
                                                        <Grid key={data.key} item container direction='row' alignItems='center' style={{marginBottom:'20px'}} >
                                                            <Grid item style={{width:'100%'}}>
                                                                <Grid container alignItems='center' >
                                                                    <CropSquareIcon style={{width:'10px',height:'10px',marginRight:'8px',color: trafficTypeColorMappings[data.key], backgroundColor: trafficTypeColorMappings[data.key]}}/>
                                                                    <Grid item>
                                                                        <Typography style={{fontSize:'16px',fontFamily:'Noto sans' ,color:'#626262'}}>{_.capitalize(data.key)}</Typography>
                                                                    </Grid>
                                                                </Grid>
                                                                <Grid container alignItems='center'>
                                                                    <Grid item lg={6} md={6}>
                                                                        <Typography style={{ fontSize: '18px', fontFamily: 'Noto sans', color: '#0E0F0F' }}>{currencyFormat(data.value)}</Typography>
                                                                    </Grid>
                                                                    
                                                                    <Grid item lg={6} md={6}>
                                                                        <Grid container alignItems='center'>
                                                                            {
                                                                                changeRate >= 0 || !_.isFinite(changeRate) ?   
                                                                                <ArrowDropUpIcon style={{color:'#52C41A'}}/>:

                                                                                <ArrowDropDownIcon style={{color:'#FE2D53'}}/>
                                                                            }
                                                                            <Typography className={classes.tableNumberRateFont} style={changeRate >= 0 || !_.isFinite(changeRate) ? {color:'#52C41A'} :{} }>{`${_.isNaN(changeRate)? '0' : !_.isFinite(changeRate) ? '100' : Math.abs(changeRate)}%`}</Typography>                       
                                                                        </Grid>
                                                                    </Grid>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                    )
                                                })}
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item container justifyContent='flex-end' alignItems='center' style={{ marginTop:'6px',  paddingRight: '12px'  }}>
                                        <Button style={{textTransform:'none',marginRight:'10px'}} onClick={() => downloadFile(masterData.trafficSources.data, 'traffic_source.json')} >
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'Export data'}</Typography>
                                            <DownloadIcon style={{ color: '#5A91FF', width:'22px', height:'22px' }}/>
                                        </Button>
                                        <Button style={{textTransform:'none'}} onClick={() => history.push('/dashboard/trafficsources')}>
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'See more detail'}</Typography>
                                            <ArrowForwardIosIcon style={{ color: '#5A91FF', width:'16px', height:'16px' }}/>
                                        </Button>
                                    </Grid>
                                    </>
                                }
                               
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

                {/* Sales funnel / Conversion Rate */}
                <Grid container direction='row' justifyContent='center' style={{marginTop:'20px'}}>
                    <Grid item container justifyContent='flex-start' lg={4} md={4}>
                        <Grid item container style={{width:'98%'}}>
                            <Grid item container className={classes.trafficCard} style={{height:'520px'}}>
                                <Grid container style={{ marginTop: matchesMac ? '15px' : '30px' }}>
                                    <Grid item style={{padding: matchesMac?'0px 25px':'0px 40px'}}>
                                        <Typography className={classes.subTitleFont} >{'Sales funnel'}</Typography>
                                    </Grid>
                                </Grid>
                                {masterData.funnels.isLoading ? 
                                <Grid container justifyContent='center' alignItems='center' style={{height:'260px'}}>
                                    <ReactLoading type={'bars'} color="#5A91FF" />
                                </Grid>
                                : 
                                <Grid container>
                                    <Grid item lg={3} md={3}>
                                        <Grid container direction='column' alignItems='center' style={{ marginTop: matchesMac ? '15px' : '35px' }}  >
                                            {   
                                                masterData.funnels.data.map((da:any) =>
                                                {
                                                    const value = _.floor(_.divide(da.value, masterData.funnels.data[0].value) * 100, 2)
                                                    const finalValue = (_.isNaN(value) || !_.isFinite(value)) ? '0' : value
                                                    return (
                                                        <Grid key={da.key} item container direction='column' alignItems='flex-end' style={{marginBottom:'24px'}}>
                                                            <Grid item style={{marginBottom:'2px'}}>
                                                                <Typography className={classes.trafficFontSm} style={{ color: '#0E0F0F', fontSize: matchesMac ? '14px' : '16px' }}>{da.key}</Typography>
                                                            </Grid>
                                                            <Grid item>
                                                                <Typography style={{ color: '#4A78DF', fontSize: matchesMac ? '14px' : '16px', fontFamily: 'Roboto' }}>{`${finalValue}%`}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                    )
                                                })
                                            }    
                                        </Grid>
                                    </Grid>
                                    <Grid item lg={9} md={9} style={{width:'80%'}}>
                                        <Grid container direction='column' alignItems='flex-start' style={{marginTop:matchesMac?'5px':'30px',width:matchesMac?'70%':'90%'}} className={classes.funnelContainer}>                                            
                                            <FunnelChart width={matchesMac?350:450} height={400}>
                                                <Funnel 
                                                    isAnimationActive={ false }
                                                    dataKey="value"
                                                    data={masterData.funnels.data?.map((data:any,index:any) =>
                                                    {
                                                        //need to add fill and presentValue column
                                                        return { ...data, ...{fill: funnelColor[index], presentValue: currencyFormat(data.value) }}
                                                    })}
                                                >
                                                    {
                                                        masterData.funnels.data.map((entry:any, index:any) => {
                                                            return(
                                                                <Cell key={`cell-${index}`} height={index===masterData.funnels.data.length-1? 140:50}  />
                                                            )
                                                        })
                                                    }
                                                    <LabelList fontSize={20} fontFamily='noto sans' position="inside" fill="#FFF" stroke="none" dataKey="presentValue" />
                                                    </Funnel>
                                                    <Tooltip content={(payload) => CustomTooltipForPie(payload,filterColumn.startDate,  filterColumn.endDate, filterColumn.compareDateRange)} />
                                            </FunnelChart>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                }
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item container justifyContent='flex-end' lg={8} md={8}>
                        <Grid item container style={{width:'98%'}}>
                            <Grid item container direction='column' className={classes.trafficCard} style={{height:'520px'}}>
                                <Grid container justifyContent='space-between' style={{ marginTop: matchesMac ? '15px' : '30px' , padding: matchesMac?'0px 25px':'0px 35px'}}>
                                    <Grid item >
                                        <Typography className={classes.subTitleFont} >{'Conversion Rate'}</Typography>
                                    </Grid>
                                    <Grid item >
                                        <Button
                                            startIcon={<AddIcon style={{color: '#5A91FF'}}/>}
                                            variant='outlined'
                                            style={{ width: '150px', height: '32px', border:'1px dashed #5A91FF' }}
                                            id="demo-positioned-button"
                                            aria-controls="demo-positioned-menu"
                                            aria-haspopup="true"
                                            aria-expanded={conversionOpen ? 'true' : undefined}
                                            onClick={conversionHandleClick}
                                        >
                                            <Typography style={{ textTransform: 'none', fontSize: '16px', color: '#5A91FF', fontFamily: 'Noto sans' }}>{'Comparison'}</Typography>
                                            
                                        </Button>
                                        <Menu
                                            id="demo-positioned-menu"
                                            aria-labelledby="demo-positioned-button"
                                            anchorEl={conversionAnchorEl}
                                            open={conversionOpen}
                                            onClose={conversionHandleClose}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                            }}
                                                transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                                }}
                                            MenuListProps={{
                                                'aria-labelledby': 'basic-button',
                                                style:{padding:0, width:'200px'}
                                            }}
                                        >
                                        <MenuItem style={{backgroundColor:conversionSelected.length === conversionCombination.length ? '#E2EEFF':''}} onClick={() => { handleConversionSelectedAll() }}>
                                            <Grid container alignItems='center'>
                                                {conversionSelected.length === conversionCombination.length ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                <Grid item>
                                                    <Typography>{'All'}</Typography>
                                                </Grid>
                                            </Grid>
                                        </MenuItem>
                                        <Divider style={{width:'100%'}}/>
                                        {conversionCombination.map(bc =>
                                        {
                                            return <MenuItem key={bc} style={{backgroundColor:_.includes(conversionSelected,bc) ?'#E2EEFF':''}} onClick={() => { handleConversionSelected(bc) }}>
                                                <Grid container alignItems='center'>
                                                    {_.includes(conversionSelected,bc) ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                    <Grid item>
                                                        <Typography>{memberTypeMappings[bc]}</Typography>
                                                    </Grid>
                                                </Grid>
                                            </MenuItem>
                                        })}
                                            
                                        </Menu>
                                    </Grid>
                                </Grid>
                                {/**
                                 * <Grid container style={{marginTop: matchesMac ? '10px' : '20px'}}>
                                    <Grid item style={{paddingLeft:'40px',width:'100%',paddingRight:'40px'}}>
                                        <Box >
                                            <Tabs value={tabValue}  onChange={handleChange} className={classes.tabs} >
                                                <Tab value={0} disableRipple 
                                                    label={<Typography style={tabValue===0?{color:'#4A78DF'}:{}} className={classes.tabFont}>All</Typography>}
                                                    style={{minWidth:'15%'}}
                                                    />
                                                <Tab value={1} disableRipple 
                                                    label={<Typography style={tabValue===1?{color:'#4A78DF'}:{}} className={classes.tabFont}>JRG Members</Typography>}
                                                    style={{minWidth:'15%'}}/>
                                                <Tab value={2} disableRipple 
                                                    label={<Typography style={tabValue===2?{color:'#4A78DF'}:{}}  className={classes.tabFont}>Yuu</Typography>}
                                                    style={{minWidth:'15%'}}/>
                                                <Tab value={3} disableRipple 
                                                    label={<Typography style={tabValue===3?{color:'#4A78DF'}:{}}  className={classes.tabFont}>Anonymous</Typography>}
                                                    style={{ minWidth: '15%' }} />
                                                <Tab value={4} disableRipple 
                                                    label={<Typography style={tabValue === 4 ? { color: '#4A78DF' } : {}} className={classes.tabFont}>{'Yuu & JRG Members'}</Typography>}
                                                    style={{ minWidth: '15%' }} />
                                                <Tab value={5} disableRipple 
                                                    label={<Typography style={tabValue===5?{color:'#4A78DF'}:{}}  className={classes.tabFont}>{'Yuu & Anonymous'}</Typography>}
                                                    style={{minWidth:'15%'}}/>
                                            </Tabs>
                                        </Box>
                                    </Grid>
                                </Grid>
                                 */}
                                
                                <Grid container direction={matchesMac?'column':'row'} justifyContent='space-between' style={{paddingLeft:'40px',width:'100%',paddingRight:'40px',marginTop:'20px'}}>
                                    <Grid item >
                                        <Grid container alignItems='center'>
                                            {conversionSelected.map((ty,index) =>
                                            {
                                                return (
                                                    <Grid item key={ty}>
                                                        <Grid container direction='row' alignItems='center' style={{marginRight:'20px'}}>
                                                            <CropSquareIcon style={{width:'10px',height:'10px',marginRight:'8px',color: memberTypeColorMappings[ty], backgroundColor: memberTypeColorMappings[ty]}}/>
                                                            <Grid item>
                                                                <Typography style={{fontSize:'14px',fontFamily:'Noto sans'}}>{memberTypeMappings[ty]}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                )
                                            })}
                                        </Grid>
                                    </Grid>
                                    <Grid item style={{ marginTop: matchesMac ? '10px' : '0px'}}>
                                        <Grid container alignItems='center'>
                                            <Grid item style={{marginRight:'8px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}></Typography>
                                            </Grid>
                                            <Grid item style={{marginRight:'20px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>{dateOptionMappings[filterColumn.period]}</Typography>
                                            </Grid>
                                            <Grid item style={{marginRight:'8px'}}>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>---</Typography>
                                            </Grid>
                                            <Grid item>
                                                <Typography style={{fontSize:'14px', fontFamily:'Noto sans'}}>{'Preceding period'}</Typography>
                                            </Grid>    
                                        </Grid>
                                    </Grid>
                                </Grid>
                                {masterData.conversions.isLoading ? 
                                <Grid container justifyContent='center' alignItems='center' style={{height:'300px'}}>
                                    <ReactLoading type={'bars'} color="#5A91FF" />
                                </Grid>
                                    : 
                                    <>
                                    <Grid container justifyContent='center' style={{marginTop: matchesMac?'25px':'40px'}}>
                                        <Grid item style={{height:'300px',width:'98%'}}>
                                            <ResponsiveContainer width="100%" height="100%">
                                                <LineChart
                                                width={500}
                                                height={300}
                                                data={masterData.conversions.data}
                                                margin={{
                                                    top: 5,
                                                    right: 30,
                                                    left: 20,
                                                    bottom: 5,
                                                }}
                                                >
                                                <CartesianGrid   vertical={false} />
                                                <XAxis dataKey="date" tick={{color: '#989898', fontFamily:'Roboto', fontWeight:300, fontSize:'14px'}}/>
                                                <YAxis tick={{color: '#989898', fontFamily:'Roboto', fontWeight:300, fontSize:'14px'}}/>
                                                    <Tooltip cursor={{ fill: '#0F0F0F', opacity: '10%' }} content={(payload) => CustomTooltipForLine(payload, memberTypeColorMappings, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange, conversionActiveLine)} />
                                                    {
                                                        conversionSelected.map((dataKey,index) =>
                                                        {
                                                            return (
                                                                <React.Fragment key={dataKey}>
                                                                    <Line type="linear" name={memberTypeMappings[dataKey]} dataKey={dataKey} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'3px'} style={{opacity: (conversionActiveLine !== dataKey && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={()=> setConversionActiveLine(dataKey)} onMouseLeave={()=> setConversionActiveLine('')}/>
                                                                    <Line type="linear" name={`_${ memberTypeMappings[dataKey] }`} dataKey={`_${ dataKey }`} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'1px'}  strokeDasharray="2 2" dot={false} style={{opacity: (conversionActiveLine !== `_${ dataKey }` && conversionActiveLine !== '') ? 0.2 : 1 }}  onMouseEnter={()=> setConversionActiveLine(`_${ dataKey }`)} onMouseLeave={()=> setConversionActiveLine('')}/>
                                                                </React.Fragment>
                                                            )
                                                        })
                                                    }
                                                    {/* {tabValue === 0 || tabValue === 1 ? <Line type="linear" name='JRG Members' dataKey="jrgMember" stroke="#4A78DF" strokeWidth={'3px'} style={{opacity: (conversionActiveLine !== 'jrgMember' && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={()=> setConversionActiveLine('jrgMember')} onMouseLeave={()=> setConversionActiveLine('')} /> : '' }
                                                    {tabValue === 0 || tabValue === 1 ? <Line type="linear" name='_JRG Members' dataKey="_jrgMember" stroke="#4A78DF" strokeWidth={'1px'} style={{opacity: (conversionActiveLine !== '_jrgMember' && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} strokeDasharray="2 2" onMouseEnter={()=> setConversionActiveLine('_jrgMember')} onMouseLeave={()=> setConversionActiveLine('')}/> : ''}
                                                    {tabValue === 0 || tabValue === 2 ? <Line type="linear" name='Yuu'  dataKey="yuu" stroke="#AC6700" strokeWidth={'3px'}  style={{opacity: (conversionActiveLine !== 'yuu' && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={()=> setConversionActiveLine('yuu')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 2 ? <Line type="linear" name='_Yuu' dataKey="_yuu" stroke="#AC6700" strokeWidth={'1px'}  strokeDasharray="2 2" style={{opacity: (conversionActiveLine !== '_yuu' && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} onMouseEnter={()=> setConversionActiveLine('_yuu')} onMouseLeave={()=> setConversionActiveLine('')} />: ''}
                                                    {tabValue === 0 || tabValue === 3 ? <Line type="linear" name='Anonymous' dataKey="anonymous" stroke="#65CBFF" strokeWidth={'3px'} style={{opacity: (conversionActiveLine !== 'anonymous' && conversionActiveLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={()=> setConversionActiveLine('anonymous')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 3 ? <Line type="linear" name='_Anonymous' dataKey="_anonymous" stroke="#65CBFF" strokeWidth={'1px'}  strokeDasharray="2 2" dot={false} style={{opacity: (conversionActiveLine !== '_anonymous' && conversionActiveLine !== '') ? 0.2 : 1 }} onMouseEnter={()=> setConversionActiveLine('_anonymous')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 4 ? <Line type="linear" name='Yuu & JRG Members'  dataKey="yuu_jrgMember" stroke="#FEC558" strokeWidth={'3px'}  dot={false} activeDot={{ r: 5 }} style={{opacity: (conversionActiveLine !== 'yuu_jrgMember' && conversionActiveLine !== '') ? 0.2 : 1 }} onMouseEnter={()=> setConversionActiveLine('yuu_jrgMember')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 4 ? <Line type="linear" name='_Yuu & JRG Members' dataKey="_yuu_jrgMember" stroke="#FEC558" strokeWidth={'1px'}  strokeDasharray="2 2" dot={false} style={{opacity: (conversionActiveLine !== '_yuu_jrgMember' && conversionActiveLine !== '') ? 0.2 : 1 }} onMouseEnter={()=> setConversionActiveLine('_yuu_jrgMember')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 5 ? <Line type="linear" name='Yuu & Anonymous' dataKey="yuu_anonymous" stroke="#8C7CEE" strokeWidth={'3px'}  dot={false} activeDot={{ r: 5 }} style={{opacity: (conversionActiveLine !== 'yuu_anonymous' && conversionActiveLine !== '') ? 0.2 : 1 }} onMouseEnter={()=> setConversionActiveLine('yuu_anonymous')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''}
                                                    {tabValue === 0 || tabValue === 5 ? <Line type="linear" name='_Yuu & Anonymous' dataKey="_yuu_anonymous" stroke="#8C7CEE" strokeWidth={'1px'} strokeDasharray="2 2" dot={false} style={{opacity: (conversionActiveLine !== '_yuu_anonymous' && conversionActiveLine !== '') ? 0.2 : 1 }} onMouseEnter={()=> setConversionActiveLine('_yuu_anonymous')} onMouseLeave={()=> setConversionActiveLine('')}/>: ''} */}
                                                </LineChart>
                                            </ResponsiveContainer>
                                        </Grid>
                                    </Grid>
                                    <Grid item container justifyContent='flex-end' alignItems='center' style={{ marginTop:'20px',  paddingRight: '12px'  }}>
                                        <Button style={{textTransform:'none',marginRight:'10px'}} onClick={() => downloadFile(masterData.conversions.data, 'conversion.json')} >
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'Export data'}</Typography>
                                            <DownloadIcon style={{ color: '#5A91FF', width:'22px', height:'22px' }}/>
                                        </Button>
                                        <Button style={{textTransform:'none'}} onClick={() => history.push('/dashboard/conversions')}>
                                            <Typography style={{color:'#5A91FF', marginRight:'8px', fontSize:'16px', fontFamily:'Noto sans'}}>{'See more detail'}</Typography>
                                            <ArrowForwardIosIcon style={{ color: '#5A91FF', width:'16px', height:'16px' }}/>
                                        </Button>
                                    </Grid>
                                    </>
                               
                                }
                                
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}

export default L_OnsitePerformance